import React, { useState } from 'react'

export default function LoadingBox() {
    const images = ["/2B-transparent-half-border_1.png","/Loading-Animation_1.gif","Loading-Animation.gif"]
    const [i, seti] = useState(0);
    if(i===images.length){
        seti(0)
    }
    return (
        <div class="loading-screen">
            <div class="loading-animation" onClick={()=>seti(i+1)}>
                <img src={images[i]} alt="" class={i===0?"logo":"gif"}/>
                {i===0&&<div class="loading-bar"></div>}
            </div>
        </div>
    )
}
