import React, { useState } from 'react'
import {Player,BigPlayButton} from 'video-react'
import Comment from './Comment'
import "../../node_modules/video-react/dist/video-react.css";
import { useDispatch, useSelector } from 'react-redux';
import { cancelLike, commentVideo, likeVideo, sharevideo, viewVideo } from '../Actions/VideoActions';
import Profile from './Profile';

export default function Video({vid}) {
    const dispatch = useDispatch()
    console.log(vid);
    const Link = vid.video_link&&vid.video_link.replace("../front-end/public","")
    const [showAddComment, setShowAddComment] = useState(false)
    let toggleLike=vid.isLiked
    const [commentText, setCommentText] = useState("")
    const userLogin = useSelector(state => state.userLogin)
    const like = ()=>{
        if(userLogin.id){
            toggleLike = !toggleLike
            if(toggleLike && !vid.isLiked){
                vid.isLiked = true
                vid.likes++;
                dispatch(likeVideo(userLogin.id,vid.id))
            }
            if(!toggleLike && vid.isLiked){
                vid.isLiked = false
                vid.likes--;
                dispatch(cancelLike(vid.id))
            }
        }
    }
    const comment = ()=>{
        if(userLogin.id&&commentText.length>0){
            vid.AllComments.push({user_id:userLogin.id,video_id:vid.id,text:commentText,name:userLogin.username})
            vid.comments++;
            dispatch(commentVideo(userLogin.id,vid.id,commentText))
            setCommentText("")
        }
    }
    const handleKeyPress = (e)=>{
        if(userLogin.id){            
            if(e.key === "Enter"){
                comment()
            }
        }
    }
    const share = ()=>{
        if(userLogin.id){
            vid.shares++;
            dispatch(sharevideo(userLogin.id,vid.id))
        }
    }
    const view = ()=>{
        if(userLogin.id){
            dispatch(viewVideo(userLogin.id,vid.id))
        }
    }
    const HandleLoad = () => {

        let elt = document.getElementById(vid.id+"");
        if(elt){
            elt= elt.querySelector(".video-react-poster");
            if(elt){
                if(elt.clientHeight>1000){
                    elt = document.getElementById(vid.id+"")
                    if(elt){
                        elt = elt.querySelector(".video")
                        elt.style.width="34.2%";
                        elt = elt.querySelector(".profile");
                        if(elt){
                            elt.style.width = "150%";
                        }
                    }
                }
            }
        }
    }
    console.log(vid);
    return (
        <div className="one-swipe" id={vid.id} onLoadedData={HandleLoad}>
        <div className="video">
            <Profile name={vid.name} id={vid.user_id} followed={vid.followed}/>
            <Player 
               
                poster={Link}
                src=/*{`https://13k13.com${Link}`}*/{Link}
                fluid = {true}
                onPlay ={view} 
                >
                <BigPlayButton position="center"/>
                <br></br>
            </Player> 
            <div className="video-elements">
                <p><i className={`fa fa-heart ${(vid.isLiked)&&"red-heart"}`} onClick={like}></i> {vid.likes} </p>
                <p><i className="fa fa-comment"  onClick={()=>setShowAddComment(!showAddComment)}></i> {vid.comments} </p>
                <p><i className="fa fa-share" onClick={share}></i> {vid.shares} </p>
                <p><i className="fa fa-eye"></i> {vid.views}</p>
            </div> 
        </div>
        
        <div className="comments">
            <h1><i className="fa fa-comment fa-2x"></i></h1>
            <div className="all-comments">
            {vid.AllComments&&vid.AllComments.length>0?vid.AllComments.map(item=>(
                <Comment name={item.name}id={vid.user_id}  text={item.text} followed={vid.followed}/>
            )):(
                <p className="comment-first">Be the first one to comment !</p>
            )} 
            </div>

            <div className="add-comment">
                <div className="toggle-add-comment" onClick={()=>setShowAddComment(!showAddComment)}>
                    <i className={`fa fa-${showAddComment?'close':'plus'}`}></i>
                </div>
                {showAddComment&&(
                    <>
                    <input type="text" placeholder="add a comment here!" autoFocus value={commentText} onKeyPress={e=>handleKeyPress(e)} onChange={e=>setCommentText(e.target.value)}/>
                    <i className="fa fa-send" onClick={comment}></i>
                    </>
                )}
            </div>

        </div>

        </div>
    )
}
